<template>
  <app-page title="MessageBox " description="">

    <app-case title="" description="">
      <zk-button type="text" @click="open"> Message Box</zk-button>
    </app-case>

    <app-case title="" description="">
      <zk-button type="text" @click="open2"> Message Box</zk-button>
    </app-case>

    <app-case title="" description="">
      <zk-button type="text" @click="open3"> Message Box</zk-button>
    </app-case>

  </app-page>
</template>

<script>
import MessageBox from '@/packages/message-box'
import Button from '@/packages/button'

export default {
  components: {
    'zk-button': Button
  },
  data () {
    return {
      gridData: []
    }
  },
  methods: {

    open () {
      MessageBox.alert('', '', {
        confirmButtonText: '',
        callback: action => {}
      })
    },

    open2 () {
      MessageBox.confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: ''
      }).then(() => {}, () => {})
    },

    open3 () {
      MessageBox.prompt('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        inputPattern: /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/,
        inputErrorMessage: ''
      }).then(() => {}, () => {})
    }
  }
}
</script>

<style lang="scss" scoped>

</style>
